* {
  box-sizing: border-box;
}
html, body {
  height: 100%;
  background: #31c27c;
}
body {
  font: 12px/1.5 "hiragino sans gb",arial;
  max-width: 10rem;
  margin: 0 auto;
}
input[type="checkbox"], input[type="radio"], select, textarea {
  -webkit-appearance: none;
}
select {
  background: transparent;
  border: none;
}
#app {
  min-height: 100%;
  background: #f4f4f4;
  position: relative;
}
#app .fade-enter-active, #app .fade-leave-active {
    will-change: opacity;
    -webkit-transition: opacity .3s;
    transition: opacity .3s;
}
#app .fade-enter, #app .fade-leave-active {
    opacity: 0;
}
#app .slide-fade-enter-active {
    -webkit-transition: all .3s ease;
    transition: all .3s ease;
}
#app .slide-fade-leave-active {
    -webkit-transition: all .3s ease;
    transition: all .3s ease;
}
#app .slide-fade-enter, #app .slide-fade-leave-active {
    -webkit-transform: translateY(100%);
            transform: translateY(100%);
}
.rippleBtn {
  position: absolute;
  background: rgba(0, 0, 0, 0.3);
  border-radius: 100%;
  -webkit-transform: scale(0);
          transform: scale(0);
  -webkit-transform-origin: center center;
          transform-origin: center center;
  -webkit-transition: all 0.75s cubic-bezier(0.25, 0.46, 0.45, 0.94);
  transition: all 0.75s cubic-bezier(0.25, 0.46, 0.45, 0.94);
}
.rippleBtnBig {
  -webkit-transform: scale(1.5);
          transform: scale(1.5);
  opacity: 0;
}
.hasPlayList {
  padding-bottom: 1.6rem;
}



